<template lang="html">
    <div class="" />
</template>

<script>
import { eventBus } from '@/helpers/event-bus/'
import {
    ACTION_CLIENT_SOCIAL_LOGIN_VERIFY
} from '@/store/modules/client/action-types'

import { createNamespacedHelpers } from 'vuex';

const {
    mapActions: mapClientActions
} = createNamespacedHelpers('client');

export default {
    data: () => ({
        code: null,
        service: null
    }),
    mounted() {
        this.code = this.$route.query.code
        this.service = this.$route.params.service

        this.submit()
    },
    methods: {
        ...mapClientActions({
            verify: ACTION_CLIENT_SOCIAL_LOGIN_VERIFY
        }),
        async submit() {
            const vm = this
            try {
                // check code, register and login client if success
                const response = await this.verify({
                    code: vm.code,
                    service: vm.service
                })

                eventBus.$emit('showSnackBar', 'Great success!', 'success');
                console.log(response);
                // let previousRoute = window.localStorage.getItem('social_login_last_visited')
                this.$router.push({ name: 'account' });
            } catch (e) {
                // console.log(e)

                // this.$store.commit('modals/MODAL_ALERT_ON', {
                //     alert_title: 'Error',
                //     alert_body: e.response.data.message
                // })

                eventBus.$emit('showSnackBar', e.response.data.message, 'error');

                setTimeout(() => {
                    vm.$router.push({ name: 'login' })
                }, 2000)
            }
        }

    }

}
</script>
