<template>
    <div class="page-section-mask">
        <section class="faq profile grid-container search-category wrapper reset-pass">
            <div class="grid__col12">
                <form
                    class="reset-form"
                    @submit.prevent="validate"
                >
                    <div class="profile-info">
                        <header class="profile-info__header">
                            <span class="lock_icon">
                                <lock />
                            </span>
                            <h2>Secure your account with a strong password</h2>
                        </header>
                        <div class="profile-info__form">
                            <div class="profile-info__form-group">
                                <div class="profile-info__form-row">
                                    <div class="profile-info__col50 profile-info__column">
                                        <form-input
                                            v-model="$v.password.$model"
                                            name="password"
                                            legend="Password"
                                            prepend-icon="LockIcon"
                                            :field.sync="password"
                                            :append-icon="showPassword ? 'EyeIcon' : 'EyeOffIcon'"
                                            :type="showPassword ? 'text' : 'password'"
                                            :errors="validationMessage($v.password)"
                                            :is-valid="$v.password.$dirty && !$v.password.$anyError"
                                            @clicked="showPassword = !showPassword"
                                            @input.native="$v.password.$touch()"
                                            @blur.native="$v.password.$touch()"
                                        />
                                        <progress-linear :validate="$v.password.$params.passwordStrengthTest" />
                                    </div>
                                    <div class="profile-info__col50">
                                        <form-input
                                            v-model="$v.repeatPassword.$model"
                                            name="password"
                                            legend="Password"
                                            prepend-icon="LockIcon"
                                            :field.sync="repeatPassword"
                                            :append-icon="showPassword ? 'EyeIcon' : 'EyeOffIcon'"
                                            :type="showPassword ? 'text' : 'password'"
                                            :errors="validationMessage($v.repeatPassword)"
                                            :is-valid="$v.repeatPassword.$dirty && !$v.repeatPassword.$anyError"
                                            @clicked="showPassword = !showPassword"
                                            @input.native="$v.repeatPassword.$touch()"
                                            @blur.native="$v.repeatPassword.$touch()"
                                        />
                                    </div>
                                </div>
                            </div>
                        </div>
                        <!--                        <transition name="slide-fade">-->
                        <!--                            <div v-if="error" class="eister-alert eister-alert-danger">-->
                        <!--                                {{message}}-->
                        <!--                                <i class="far fa-exclamation-triangle"></i>-->
                        <!--                            </div>-->
                        <!--                        </transition>-->
                        <footer class="profile-info__footer justify-center">
                            <button
                                :loading="loading"
                                class="btn-base btn-base_colored btn-base_normal login_button reset_btn"
                            >
                                <i class="far fa-save" />Submit
                            </button>
                        </footer>
                    </div>
                </form>
            </div>
        </section>
    </div>
</template>

<script>
// EventBus
import { eventBus } from '@/helpers/event-bus/'

// API
import Api from '@/helpers/api/index.js'

// MDI
import Lock from 'mdi-vue/Lock.vue'

// vuelidate
import { validationMessage } from 'vuelidate-messages'
import { validationMixin } from 'vuelidate'
import passwordValidationMixin from '@/mixins/passwordValidationMixin'
import formValidationMixin from '@/mixins/formValidationMixin'
import { validationRules } from '@/validation/reset-password/Rules'
import { formMessages } from '@/validation/reset-password/Messages'

// elements
import FormInput from '@/components/common/form-elements/FormInput'

// vuex
import { createNamespacedHelpers } from 'vuex'
import { ACTION_CLIENT_RESET_PASSWORD_SET_NEW } from '@/store/modules/client/action-types'

const { mapActions: mapClientActions } = createNamespacedHelpers('client')

export default {
    name: 'ResetPasswordForm',
    components: {
        FormInput,
        Lock
    },
    mixins: [validationMixin, formValidationMixin, passwordValidationMixin],
    validations: {
        ...validationRules
    },
    data() {
        return {
            showPassword: false,
            email: '',
            loading: false,
            password: '',
            repeatPassword: ''
        }
    },
    mounted() {
        if (!this.$route.params.token) {
            eventBus.$emit(
                'showSnackBar',
                'The link has expired, you need to use the reset password form again.',
                'info'
            )
            this.$router.push({ name: 'forgot-password' })
        }
        this.getData()
    },
    methods: {
        ...mapClientActions([ACTION_CLIENT_RESET_PASSWORD_SET_NEW]),
        validationMessage: validationMessage(formMessages),
        getData() {
            Api.post('/api/auth/client/validate-reset-token', {
                token: this.$route.params.token
            })
                .then((response) => {
                    // set users email for which to update password
                    this.email = response.data
                })
                .catch(() => {
                    this.expired = true
                    eventBus.$emit(
                        'showSnackBar',
                        'The link has expired, you need to use the reset password form again.',
                        'info'
                    )
                    this.$router.push({ name: 'forgot-password' })
                })
        },
        submit() {
            try {
                this.loading = true
                this[ACTION_CLIENT_RESET_PASSWORD_SET_NEW]({
                    token: this.$route.params.token,
                    password: this.password,
                    email: this.email
                })
                eventBus.$emit('showSnackBar', 'Your password has been changed', 'success')
                this.$router.push({ name: 'login' })
            } catch (err) {
                eventBus.$emit('showSnackBar', err.response.data.message, 'error')
            } finally {
                this.loading = false
            }
        }
    }
}
</script>

